.header{
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 5rem;
    background-color: #8a2b06;
     color: white;
     display: flex;
     justify-content: space-between;
     text-align: center;
     padding: 0 10%;
     box-shadow: 0 2px 8px rgba(0, 0, 0, 0.25);
     z-index: 10;
}



.main-image{
    width: 100%;
    height: 25rem;
    z-index: 0;
    overflow: hidden;
    top: 0;
    left: 0;
}

.mian-image img{
    width: 100%;
    height: 100%;
     object-fit: cover;
    transform: rotateZ(-5deg) translateY(-5rem) translateX(-1rem);
}